* {
  font-family: "Roboto", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  
}

:root {
  --text-left-color: #797979;
  --text-color: #303030;
  --text-size-xs: 1.3vw;
  --text-size-sm: 2vw;
  --text-size-md: 3.5vw;
  --text-size-lg: 6vw;
  --background: #E9E9E9;
}

body {
  height: 1600px;
  color: var(--text-color);
}

body::-webkit-scrollbar {
  display: none;
}

.top-left-sticky {
  position: fixed;
  top: 25px;
  margin-left: 25px;
  font-size: var(--text-size-sm);
  color: var(--text-left-color);
}

.invisible {
  opacity: 0;
  transition: opacity 400ms;
}

.top-right-sticky {
  width: 420px;
  position: fixed;
  top: 25px;
  right: 25px;
  text-align: right;
  margin-right: 25px;
  font-size: var(--text-size-md);
}

.big-name-container {
  margin-top: 100px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.big-name {
  font-size: 130px;
  margin: 10px;
  opacity: 0;
}


.bottom-right {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  top: 80%;
  right: 20px;
}

#code {
  width: 100px;
  margin: auto;
}

#link {
  color: var(--text-color);
  font-size: var(--text-size-sm);
  text-decoration: underline;
}

.arrow {
  position: absolute;
  top: 80%;
  left: 49%;
  transform: rotate(90deg);
}

#scroll {
  height: 80px;
}

.second-scroll {
  opacity: 0;
}

.third-scroll {
  opacity: 0;
}

.fourth-scroll {
  opacity: 0;
}

.fifth-scroll {
  opacity: 0;
}

.second-page {
  margin-top: 20%;
  margin-left: 10%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.about-text {
  color: var(--text-color);
  font-size: var(--text-size-sm);
}

.blog { 
  font-size: 24px;
  margin-right: 50px;
}

.blog-box {
  border: 10px solid black;
  height: 100px;
  margin-top: 10px;
  text-align: center;
}

#blog-link {
  font-size: 68px;
  font-weight: 700;
  color: black;
  text-decoration: none;
}

.skill-container {
  margin-top: -16%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.box {
  width: 12vw;
  margin: 2vw;
  height: 12vw;
  background-color: var(--background);
  padding-top: 5vw;
  font-size: var(--text-size-sm);
  text-align: center;
}

.skills-text {
  text-align: center;
  font-size: var(--text-size-sm);
  margin: 1vw;
}

.projects-container {
  margin-top: -26%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.project-box {
  width: 30vw;
  height: 10vw;
  background-color: var(--background);
  margin: 2vw;
  display: flex;
}

.project-img {
  width: 16vw;
  height: 98%;
  border: 1px solid lightgray;
  margin: 2px;
}

.project-head {
  font-size: var(--text-size-sm);
  margin-left: 6vw;
}

.info-more {
  margin-top: 1.5vw;
  margin-left: 1.5vw;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.info-more > p { 
  font-size: var(--text-size-xs);
}

.github >img,
.live > img {
  width: 30px;
  height: 30px;
}

.github,
.live {
  margin: 20px;
}

.info-links {
  display: flex;
}

a {
  z-index: 9999;
}

.fifth-scroll {
  margin-top: -25%;
}

.contact-text {
  display: flex;
  flex-direction: column;
  text-align: center;
}

#big {
  font-size: var(--text-size-lg);
  color: var(--text-color);
}

#mid {
  font-size: var(--text-size-sm);
  color: var(--text-color);
  margin-top: 4vw;
}

#sm {
  font-size: var(--text-size-sm);
  color: var(--text-left-color);
  margin-top: 2vw;
}

.contact-img-container {
  display: flex;
  justify-content: center;
}

.contact-img {
  width: 50px;
  margin: 50px;
}

.contact-notes {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  text-align: center;
  font-size: var(--text-size-sm);
}

.contact-notes > p {
  margin: 10px;
  letter-spacing: 4px;
}